import React from "react";
import Orange from "./orange.gif";


class rendImage extends React.Component {
  state = {
    open: true,
  };
  toggleOpenClosed = () => {
    this.setState((prevState) => ({
      open: !prevState.open,
    }));
  };
  render() {
    return (
      <div id="teste">
        {this.state.open ? (
          <div id="popup-container">
            <img
              onClick={this.toggleOpenClosed}
              id="close-popup"
              src={"https://img.icons8.com/all/500/close-window.png"}
              width={40}
              alt="x - exit"
            />
            <img id="img-popup" src={Orange} alt="foto-cafeteria" />
          </div>
        ) : (
          <div id="popup-container" style={{ display: "none" }}>
          </div>
        )}
      </div>
    );
  }
}

export default rendImage;
