import React from "react";
import PropTypes from "prop-types";
import { Books } from "./Book";
import { Hiring } from "./Hiring";
import { NotHiring } from "./NotHiring";
import RendImage from "./rendImage";

class Library extends React.Component {
  static defaultProps = {
    books: [{ title: "Pinoquio", author: "Walt Disney", pages: 80 }],
  };

  state = {
    open: true,
    freeBookmark: true,
    hiring: true,
  };

  toggleOpenClosed = () => {
    this.setState((prevState) => ({
      open: !prevState.open,
    }));
  };
  render() {
    console.log(this.state.data);
    const { books } = this.props;
    return (
      <div>
        <RendImage />
        <header>
          <h1>E-Library</h1>
        </header>
        <h4>
          {this.state.open ? (
            <div style={{ display: "flex" }}>
              <div id="green"> </div> <spam id="title-messenger">Online</spam>
            </div>
          ) : (
            <div style={{ display: "flex" }}>
              <div id="red"> </div>
              <spam id="title-messenger">Offline</spam>
            </div>
          )}
        </h4>
        <button id="button-messenger" onClick={this.toggleOpenClosed}>
          LibMessenger
        </button>
        <br />
        <div id="book-list-container">
          {books.map((element, id) => (
            <Books
              key={id}
              image={element.image}
              title={element.title}
              author={element.author}
              pages={element.pages}
              freeBookmark={this.state.freeBookmark}
            />
          ))}
        </div>
        <div id="footer">{this.state.hiring ? <Hiring /> : <NotHiring />}</div>
      </div>
    );
  }
}

